.intro {
 display: flex;
 flex-direction: column;
 align-items: center;

    background-image: url(/img/bg-frame.svg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    position: relative;


    &__title {
        width: 511px;
        text-align: center;
        font-weight: 500;
        font-size: 38px;
        line-height: 45px;
        text-align: center;
        color: $white;
        position: absolute;
        top: 29%;
        left: 31%;
    }
    &__img {
        padding-top: 122px;
        margin-right: 167px;
    }

    &__scroll {

        position: absolute;
        bottom: 6%;
        left: 48%;

        & a {
            content: "";
            background-image: url(../../img/scroll-down.svg);
            width: 18px;
            height: 9px;
            background-repeat: no-repeat;
            background-position: center center;
            padding: 10px 18px 12px;
            border: 1px solid #FFFFFF;
            opacity: 0.5;
            border-radius: 50%;
        }

    }

}
